import { CART_ADD_ITEM, CART_REMOVE_ITEM } from '../constants/cartConstants'

export const cartReducer = (state = { cartItems: [] }, action) => {
    switch (action.type) {
        case CART_ADD_ITEM:
            const item = action.payload
            const existItem = state.cartItems.find(x => {
                return x.product === item.product
            })

            if (existItem) {                    // if it does exist 
                console.log(existItem)
                return {
                    ...state,
                    cartItems: state.cartItems.map(x => {
                        return x.product === existItem.product ? item : x
                    })
                }
            } else {                            // if it doesn't exist
                return {
                    ...state,
                    cartItems: [...state.cartItems, item]
                }
            }
        case CART_REMOVE_ITEM:
            return {
                ...state,
                cartItems: state.cartItems.filter(item => item.product !== action.payload)    // payload is id from cartActions/removeFromCart()
            }

        default:
            return state
    }
}

